import '../styles/component.scss';

import ColorPicker from 'simple-color-picker';
import hex2rgb from './utils/hex2rgb';
import rgb2hex from './utils/rgb2hex';

const DEFAULT = {
	containerId: 'colorPickerId',
	color: 'FFFFFF',
};


export class RedsignTuningOptionColor {

	options = {}

	container = null;
	colorPickerNode = null;
	fieldRed = null;
	fieldGreen = null;
	fieldBlue = null;
	fieldHex = null;

	colorPicker = null;
	variants = [];
	sets = [];

	#activeSet = null;
	#activeVariant = null;
	#activeColor = '';

	get activeSet()
	{
		return this.#activeSet;
	}

	set activeSet(set)
	{
		if (this.#activeSet == set)
			return;

		this.sets.forEach(item => item.classList.remove('active'));
		if (!this.sets.includes(set))
		{
			this.#activeSet.classList.remove('active');
			this.#activeSet = null;
			return;
		}

		this.#activeSet = set;

		set.classList.add('active');

		const setScheme = JSON.parse(set.getAttribute('data-value'));
		const keys = Object.keys(setScheme);

		let activeVariant = null;
		for (let index in this.variants)
		{
			let variant = this.variants[index];
			let variantKey = variant.getAttribute('data-valkey');

			if (!keys.includes(variantKey))
			{
				variant.style.display = 'none';
				continue;
			}

			variant.style.display = '';
			this.setVariantColor(variant, setScheme[variantKey]);

			if (keys[0] == variantKey)
			{
				activeVariant = variant;
			}
		}

		if (activeVariant)
		{
			this.activeVariant = activeVariant;
		}
	}

	get activeVariant()
	{
		return this.#activeVariant;
	}

	set activeVariant(variant)
	{
		if (this.#activeVariant == variant)
		{
			this.activeColor = variant.querySelector('input').value;
			return;
		}

		this.variants.forEach(item => item.classList.remove('active'));

		if (!this.variants.includes(variant))
			throw new Error('Incorrected value');

		this.#activeVariant = variant;

		variant.classList.add('active');

		this.activeColor = variant.querySelector('input').value;
	}

	get activeColor()
	{
		return this.#activeColor;
	}

	set activeColor(color)
	{
		try
		{
			color = color.replace('#', '');

			if (this.#activeColor == color)
				return;

			this.#activeColor = color;

			this.setColorFields(color);
			this.setVariantColor(this.activeVariant, color);

			if (this.colorPicker)
				this.colorPicker.setColor(color);

			const activeScheme = JSON.stringify(this.getActiveScheme()).toUpperCase();
			let count = 0;
			for (let index in this.sets)
			{
				const setScheme = this.sets[index].getAttribute('data-value').toUpperCase();
					if (activeScheme == setScheme)
				{
					this.activeSet = this.sets[index];
					break;
				}
				else
				{
					count++;
				}
			}

			if (count == this.sets.length)
			{
				this.activeSet = null;
			}
		}
		catch (e)
		{
			console.error(e.message);
		}
	}

	constructor(options = {})
	{
		this.options = {...DEFAULT, ...options};

		document.addEventListener("DOMContentLoaded", this.init.bind(this));
	}

	init()
	{
		this.container = document.getElementById(this.options.containerId);

		if (!this.container)
			return;

		this.colorPickerNode = this.container.querySelector('[data-entity="colorpicker"]');
		this.fieldRed = this.container.querySelector('[data-entity="field-red"]');
		this.fieldGreen = this.container.querySelector('[data-entity="field-green"]');
		this.fieldBlue = this.container.querySelector('[data-entity="field-blue"]');
		this.fieldHex = this.container.querySelector('[data-entity="field-hex"]');

		this.sets = [...this.container.querySelectorAll('[data-entity="set-item"]')];
		this.variants = [...this.container.querySelectorAll('[data-entity="variant"]')];

		const activeSet = this.sets.filter(set => set.classList.contains('active'))[0];
		if (activeSet)
			this.activeSet = activeSet;
		else
		{
			this.activeVariant = this.variants.filter(variant => variant.classList.contains('active'))[0];
		}

		if (this.variants)
		{
			this.variants.forEach(item => {
				item.addEventListener(
					'click',
					({ currentTarget }) => {
						this.activeVariant = currentTarget;
					}
				);
			});
		}

		if (this.sets)
		{
			this.sets.forEach(item => {
				item.addEventListener(
					'click',
					({ currentTarget }) => {
						this.activeSet = currentTarget;
					}
				);
			});
		}

		if (this.fieldHex)
		{
			['blur', 'input'].forEach(
				event => this.fieldHex.addEventListener(
					event,
					BX.debounce(({ target }) => {
						this.activeColor = target.value;
					}, 500)
				)
			);
		}


		[this.fieldRed, this.fieldGreen, this.fieldBlue].forEach(
			field => {

				if (!field) return;

				['blur', 'input'].forEach(
					event => field.addEventListener(
						event,
						BX.debounce(() => {
							this.activeColor = rgb2hex(
								this.fieldRed.value,
								this.fieldGreen.value,
								this.fieldBlue.value
							);
						}, 500)
					)
				);
			}
		);

		if (this.fieldHex)
		{
			['blur', 'keyup'].forEach(
				event => this.fieldHex.addEventListener(
					event,
					BX.debounce(({ target }) => {
						this.activeColor = target.value;
					}, 500)
				)
			);
		}

		if (this.colorPickerNode)
		{
			this.colorPicker = new ColorPicker({
				el: this.colorPickerNode,
				color: this.options.color,
			})

			if (this.colorPicker)
			{
				this.setColorPickerSize();
				this.colorPicker.onChange(() =>{
					const hex = this.colorPicker.getHexString();
					this.activeColor = hex;
				});

				window.addEventListener('resize', BX.debounce(() => {
					this.setColorPickerSize();
				}, 200));
			}
		}

		if (!this.isVisible())
			BX.addCustomEvent('rs.tuning.tabs.after.change', this.setColorPickerSize.bind(this));
	}

	setColorPickerSize()
	{
		if (!this.colorPicker)
			return;

		const style = getComputedStyle(this.colorPickerNode);

		const height = parseInt(style.height, 10);
		const width = parseInt(style.width, 10);

		this.colorPicker.setSize(width, height);

		// fix selector position
		this.colorPicker.setColor(this.colorPicker.getHexString());
	}

	setColorFields(hex)
	{
		const rgb = hex2rgb(hex);

		if (this.fieldRed)
			this.fieldRed.value = Math.round(rgb.r);

		if (this.fieldGreen)
			this.fieldGreen.value = Math.round(rgb.g);

		if (this.fieldBlue)
			this.fieldBlue.value = Math.round(rgb.b);

		if (this.fieldHex)
			this.fieldHex.value = hex.replace('#', '');
	}

	isVisible()
	{
		return (this.container.offsetParent !== null);
	}

	setVariantColor(variant, hex)
	{
		const input = variant.querySelector('input');
		if (input)
		{
			input.value = hex;
			input.dispatchEvent(new Event('change'));
		}

		const paint = variant.querySelector('[data-entity="variant-paint"]');
		if (paint)
			paint.style.backgroundColor = '#' + hex;
	}

	getActiveScheme()
	{
		const scheme = {};

		for (let index in this.variants)
		{
			scheme[this.variants[index].getAttribute('data-valkey')] = this.variants[index].querySelector('input').value;
		}

		return scheme;
	}
}
